import React from 'react';
import { connect } from 'react-redux';

const LeftAnimals = (props) => {
    // console.log(props.random)
    let hostName = `${window.location.protocol}//${window.location.hostname}`;
        if(hostName === 'http://localhost') {
            hostName = 'http://animals01.vbrqx.com';
        }
    let pictures = [];
    if(!isEmpty(props.state)) {
        // console.log(props.state.pictures);
        pictures = props.state.pictures;
    }
    const getAnimalsPic = (index) => {
        let link='';
        if(pictures.length>0) {
            pictures.forEach((item) => {
                if(item.index === index) {
                    link=hostName+'/'+item.img;
                }
            });
        }
        // console.log(link);
        return link;
    }
    // getAnimalsPic('011_004');
    
    return(
        <div className="left_animals_wrapper">
            <a className="left_lion_kid" href="#"><img src={getAnimalsPic('011_004')} alt="lion" /></a>
            <a className="left_toucan_big" href="#"><img src={getAnimalsPic('011_008')} alt="toucan big" /></a>
            {
                props.random===1 && <a className="left_rhino" href="#"><img src={getAnimalsPic('011_007')} alt="rhino" /></a>
            }
            {
                props.random===1 && <a className="left_toucan_small" href="#"><img src={getAnimalsPic('011_031')} alt="toucan" /></a>
            }
            <a className="left_zebra_big" href="#"><img src={props.random<=1?getAnimalsPic('011_036'):getAnimalsPic('011_035')} alt="zebra" /></a>
            <a className="left_panda_head" href="#"><img src={getAnimalsPic('011_033')} alt="panda" /></a>
            <a className="left_giraffe_big" href="#"><img src={getAnimalsPic('011_034')} alt="giraffe" /></a>
            <a className="left_snake_small" href="#"><img src={getAnimalsPic('011_039')} alt="snake" /></a>
        </div>
    );
}

const mapStateToProps = (state) => {
    return state.main_data;
}

function isEmpty(obj) {
    for(var key in obj) {
        if(obj.hasOwnProperty(key))
            return false;
    }
    return true;
}
export default connect(mapStateToProps)(LeftAnimals);

